.HomePage {
	min-height: 100vh;
	background-color: #000000;
}

.Display-Filters {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	margin-top: 1em;
	justify-content: space-evenly;
}

.Paginado {
	color: #b3b3b3;
	font-size: 50px;
	display: flex;
	align-items: center;
}

.Paginado span {
	margin: 0 0.3em 0 0.3em;
	text-shadow: 2px 2px black;
}

.Order {
	display: flex;
}

.Finder-By-Name input,
.Paginado button,
.Order-By-Temperament select,
.Order-By-Name select,
.Order-By-Breed select {
	padding: 6px;
	font-size: 17px;
	border-width: 1px;
	border-color: #cccccc;
	background-color: #ffffff;
	color: #000000;
	border-style: hidden;
	border-radius: 7px;
	box-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
}

.Finder-By-Name input:focus {
	outline: none;
	border: 1px solid var(--main-color);
	box-shadow: 0 0 10px var(--main-color);
}
